/* eslint-disable react-hooks/exhaustive-deps */
import { ReactSVG } from "react-svg";
import { useStore } from "zustand";
import shallow from "zustand/shallow";
import { useEffect, useLayoutEffect, useRef } from "react";
import Input from "@mui/material/Input";
import InputAdornment from "@mui/material/InputAdornment";
import AccountCircle from "@mui/icons-material/AccountCircle";
import ButtonGroup from "@mui/material/ButtonGroup";
import Button from "@mui/material/Button";
import { useDebounceCallback } from "@react-hook/debounce";
import { createActivityStore } from "./store/activity";
import { useEditActivityLogic } from "./logic/useEditActivityLogic";
import { findTask, TaskType } from "../../entities/Activity";
import { OperationButtonGroup } from "./components/OperationButtonGroup";
import { TYPE_MAP } from "./const";
import { FormItem } from "./components/FormItem";
import { ListOperation } from "./components/ListOperation";
import { activityStorage } from "../../storage/activity";
import { CopyButtonGroup } from "./components/CopyButtonGroup";

export function Editor() {
  const activityStore = useRef(
    createActivityStore(activityStorage.get() ?? undefined)
  ).current;
  const { url, activity, allowRedo, allowUndo, pngUrl, uml } = useStore(
    activityStore,
    ({ url, pngUrl, uml, activity, undoIndex, operationQueue }) => ({
      url,
      activity,
      allowUndo: undoIndex < operationQueue.length - 1,
      allowRedo: undoIndex !== 0,
      operationQueue,
      pngUrl,
      uml,
    }),
    shallow
  );

  const currentTask = useStore(
    activityStore,
    ({ currentTask, activity }) =>
      !activity ? undefined : findTask(activity?.start, currentTask?.id),
    shallow
  );

  const {
    // init
    handleMount,
    // activity
    // handleTitleChange,
    handleActivityChange,
    handleRedo,
    handleUndo,
    // task
    handleAddTask,
    handleDeleteTask,
    handleSelectTask,
    handleTaskNameChange,
    // switch
    handleAddCondition,
    handleDeleteCondition,
    handleConditionTextChange,
    // while
    handleWhileConditionChange,
    // parallel
    handleAddParallelTask,
    handleDeleteParallelTask,
  } = useEditActivityLogic([activityStore]);

  const debouncedHandleActivityChange = useDebounceCallback(
    handleActivityChange,
    500
  );

  useLayoutEffect(() => {
    handleMount();
  }, []);

  useEffect(() => {
    debouncedHandleActivityChange();
  }, [activity]);

  return (
    <div className="flex flex-col h-screen">
      <div className="flex p-4 items-center border-b-solid border-gray-700 border-b-2 h-16 justify-between">
        <div className="font-bold text-xl">PlantUML Editor</div>
        <CopyButtonGroup uml={uml} png={pngUrl} svg={url} />
      </div>
      <div className="flex" style={{ height: `calc(100vh - 64px)` }}>
        <div
          style={{ width: 300 }}
          className="px-4 py-4 h-full flex-shrink-0 border-r-solid border-gray-700 border-r-2 overflow-auto"
        >
          <FormItem
            label=""
            content={
              <ButtonGroup size="small">
                <Button disabled={!allowUndo} onClick={handleUndo}>
                  
                </Button>
                <Button disabled={!allowRedo} onClick={handleRedo}>
                  
                </Button>
              </ButtonGroup>
            }
          />
          {currentTask && (
            <>
              {/*  */}
              <FormItem
                label=""
                content={
                  <Input
                    startAdornment={
                      <InputAdornment position="start">
                        <AccountCircle />
                      </InputAdornment>
                    }
                    value={TYPE_MAP[currentTask.type]}
                    disabled
                  />
                }
              />
              {/*  */}
              {currentTask.type !== TaskType.start && (
                <FormItem
                  label=""
                  content={
                    <Input
                      startAdornment={
                        <InputAdornment position="start">
                          <AccountCircle />
                        </InputAdornment>
                      }
                      value={currentTask.name}
                      onChange={(e) => {
                        handleTaskNameChange(currentTask.id, e.target.value);
                      }}
                    />
                  }
                />
              )}
              {/*  */}
              <FormItem
                label=""
                content={
                  <OperationButtonGroup
                    group={[
                      TaskType.normal,
                      TaskType.switch,
                      TaskType.parallel,
                      TaskType.while,
                      TaskType.stop,
                    ]}
                    onClick={(type) => handleAddTask(currentTask.id, type)}
                  />
                }
              />
              {/*  */}
              <FormItem
                label=""
                content={
                  <ButtonGroup variant="outlined" size="small">
                    <Button
                      onClick={() => {
                        handleDeleteTask(currentTask.id);
                      }}
                      disabled={!currentTask.prev}
                      color="error"
                    >
                      
                    </Button>
                    {currentTask.next?.type === TaskType.stop && (
                      <Button
                        onClick={() => {
                          handleDeleteTask(currentTask.next.id);
                        }}
                        color="error"
                      >
                        
                      </Button>
                    )}
                  </ButtonGroup>
                }
              />
              {currentTask.type === TaskType.while ? (
                <FormItem
                  label=""
                  content={
                    <div>
                      <Input
                        startAdornment={
                          <InputAdornment position="start">
                            <AccountCircle />
                            <div className=" ml-1 text-xs"></div>
                          </InputAdornment>
                        }
                        value={currentTask?.condition?.yes}
                        onChange={(e) =>
                          handleWhileConditionChange(
                            currentTask.id,
                            e.target.value,
                            currentTask.condition?.no ?? ""
                          )
                        }
                        placeholder=""
                      />
                      <Input
                        startAdornment={
                          <InputAdornment position="start">
                            <AccountCircle />
                            <div className=" ml-1 text-xs"></div>
                          </InputAdornment>
                        }
                        value={currentTask?.condition?.no}
                        onChange={(e) =>
                          handleWhileConditionChange(
                            currentTask.id,
                            currentTask.condition?.yes ?? "",
                            e.target.value
                          )
                        }
                        placeholder=""
                      />
                    </div>
                  }
                />
              ) : null}
              {currentTask.type === TaskType.parallel ? (
                <div>
                  <FormItem
                    label=""
                    content={
                      <ListOperation
                        allowDelete={currentTask.parallel.length > 2}
                        allowEdit={false}
                        values={currentTask.parallel.map(
                          (item, index) => ` ${index + 1}${item.name}`
                        )}
                        onDelete={(index) =>
                          handleDeleteParallelTask(currentTask.id, index)
                        }
                      />
                    }
                  />
                  <FormItem
                    label=""
                    content={
                      <OperationButtonGroup
                        group={[
                          TaskType.normal,
                          TaskType.switch,
                          TaskType.parallel,
                          TaskType.while,
                        ]}
                        onClick={(type) =>
                          handleAddParallelTask(currentTask.id, type)
                        }
                      />
                    }
                  />
                </div>
              ) : null}
              {currentTask.type === TaskType.switch ? (
                <div>
                  <FormItem
                    label=""
                    content={
                      <ListOperation
                        allowDelete={currentTask.cases.length > 1}
                        allowEdit
                        values={currentTask.cases.map((item) => item.condition)}
                        onDelete={(index) =>
                          handleDeleteCondition(currentTask.id, index)
                        }
                        onChange={(text, index) => {
                          handleConditionTextChange(
                            currentTask.id,
                            index,
                            text
                          );
                        }}
                      />
                    }
                  />
                  <FormItem
                    label=""
                    content={
                      <OperationButtonGroup
                        group={[
                          TaskType.normal,
                          TaskType.switch,
                          TaskType.parallel,
                          TaskType.while,
                          TaskType.stop,
                        ]}
                        onClick={(type) =>
                          handleAddCondition(currentTask.id, type)
                        }
                      />
                    }
                  />
                </div>
              ) : null}
            </>
          )}
        </div>
        <div className="h-full w-full overflow-auto bg-gray-100">
          <div
            className="process"
            onClick={(e: any) => {
              if (
                e.target.nodeName === "ellipse" &&
                e.target.parentNode.nodeName === "g" &&
                e.target.parentNode.firstChild === e.target
              ) {
                handleSelectTask(activity.start.id);
              } else if (e.target?.attributes?.taskId?.value) {
                handleSelectTask(e.target?.attributes?.taskId?.value);
              }
            }}
          >
            <div className="p-4">{activity && <ReactSVG src={url} />}</div>
          </div>
        </div>
      </div>
    </div>
  );
}
